.layout_ef {
  height: 100%;
  overflow-y: hidden;
}
.layout_ef .process {
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #212121;
  letter-spacing: 0;
  line-height: 16px;
  font-weight: 400;
}
.layout_ef .process .processop {
  flex-shrink: 0;
  margin-left: 15px;
}
.layout_ef .process .processop .processcontinuebtn {
  margin-right: 15px;
}
.layout_ef .process .processop .successdiv .success {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #1BA854;
  letter-spacing: 0;
  line-height: 16px;
  font-weight: 400;
}
.layout_ef .process .processop .faildiv {
  margin-left: 15px;
}
.layout_ef .process .processop .faildiv .fail {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #FB2323;
  letter-spacing: 0;
  line-height: 16px;
  font-weight: 400;
}
.layout_ef .opdiv {
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #276FF5;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 400;
  align-items: center;
  vertical-align: middle;
}
.layout_ef .opdiv div {
  width: 50%;
}
.layout_ef .pagination {
  justify-content: flex-end;
  background-color: #fff;
  padding-bottom: 5px;
  height: 8%;
}
.layout_ef table {
  table-layout: auto;
}
.layout_ef .task_table {
  height: 92%;
}
